:root {
    --HighlightColor: #1a1a19;
    --TitleColor: #4f8131;
    --ContentColor: #80993c;
    --BackgroundColor: #f6fcdf;
}

@import url("https://fonts.googleapis.com/css2?family=Gabarito:wght@400..900&family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&family=Roboto+Slab:wght@100..900&family=Vesper+Libre:wght@400;500;700;900&display=swap");

body {
    background-color: var(--BackgroundColor);
    color: var(--TitleColor);
    font-family: Libre Baskerville;
}

a {
    text-decoration: none;
    color: var(--HighlightColor);
    transition: all 0.15s;
    margin-top: 5px;
}
a:hover {
    color: var(--TitleColor);
    text-shadow: 0px 0px 5px;
    filter: blur(0.77px);
}

p {
    color: var(--ContentColor);
}

@media (prefers-color-scheme: dark) {
    :root {
        --HighlightColor: #f6fcdf;
        --TitleColor: #80993c;
        --ContentColor: #4f8131;
        --BackgroundColor: #1a1a19;
    }
}
